.about__container {
    display: grid;
    grid-template-columns: 30% 54%;
    gap: 16%;
}

.about__Ahsan {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__Ahsan-image {
    aspect-ratio: 1/1;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(20deg);
    transition: var(--transition);
}

.about__Ahsan-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-primary-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.about__card h5 {
    font-size: 0.95rem;
    color: var(--color-primary);
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}


.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* media queries for medium devices */
@media screen and (max-width: 1024px) {
    .about__container {
        /* display: grid; */
        /* grid-template-columns: 30% 60%;
        gap: 10%; */
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__Ahsan {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }

}

/* media queries for small devices */

@media screen and (max-width: 600px) {
    .about__Ahsan {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}